<template>
    <main id="daily-report">
        <daily-report-form />
        <daily-report-report />
        <daily-report-assign-cycle-dialog />
        <daily-report-funded-billing-dialog />
        <daily-report-blood-billing-dialog />
    </main>
</template>

<script>
import { onMounted, provide, reactive, ref, watch } from "vue";

import DailyReportForm from "@/components/daily-report/form.vue";
import DailyReportReport from "@/components/daily-report/report.vue";
import DailyReportAssignCycleDialog from "@/components/daily-report/assign-cycle-dialog.vue";
import DailyReportFundedBillingDialog from "@/components/daily-report/funded-billing-dialog.vue";
import DailyReportBloodBillingDialog from "@/components/daily-report/blood-billing-dialog.vue";
import { useStore } from "vuex";

export default {
    components: {
        DailyReportForm,
        DailyReportReport,
        DailyReportAssignCycleDialog,
        DailyReportFundedBillingDialog,
        DailyReportBloodBillingDialog
    },
    setup() {
        const store = useStore();

        const reportLoading = ref(false);
        provide("reportLoading", reportLoading);

        const assignCycleDialog = ref(false);
        provide("assignCycleDialog", assignCycleDialog);

        const cycleMonitoringBtnLoading = ref(false);
        provide("cycleMonitoringBtnLoading", cycleMonitoringBtnLoading);

        const fundedBillingDialog = ref(false); // todo
        provide("fundedBillingDialog", fundedBillingDialog);

        const bloodBillingDialog = ref(false);
        provide("bloodBillingDialog", bloodBillingDialog);

        const selectedDoctor = ref([]);
        provide("selectedDoctor", selectedDoctor);

        const selectedLocation = ref([]);
        provide("selectedLocation", selectedLocation);

        watch(selectedDoctor, (newValue) => {
            store.commit("dailyReport/SET_SELECTED_DOCTORS", newValue);
        });

        const reportForm = reactive({
            fromDate: new Date(),
            toDate: new Date()
        });
        provide("reportForm", reportForm);

        onMounted(() => {
            store.commit("dailyReport/SET_IS_DAILY_REPORT", false);
        });
    }
};
</script>

<style>
#daily-report .p-card .p-card-content {
    padding: 0 0;
}

#daily-report .p-field {
    margin-bottom: 0.5rem;
}

#daily-report .p-card {
    height: 100%;
}
</style>
